import React, { useState, useEffect } from "react";
import axios from "axios";
import "./style.css";
const Body = (props) => {
  const [traks, setTraks] = useState([]);
  useEffect(() => {
    try {
      axios.get("js/data.json").then((res) => setTraks(res.data));
    } catch (error) {
      console.error(error);
    }
  }, []);
  const Traks = traks.map((trak) => {
    return (
      <li
        key={trak.id}
        onClick={props.runTrack}
        className={trak.id === 1 ? "active" : ""}
        data-src={trak.src}
      >
        <audio src={trak.audio}></audio>
        <span className="audioNum">
          {trak.id > 9 ? trak.id : "0" + trak.id}
        </span>
        <span className="audioName">{trak.name}</span>
        <div>
          <span></span>
        </div>
      </li>
    );
  });
  return (
    <div className="audio">
      <div className="overlay"></div>
      <ul>{Traks}</ul>
    </div>
  );
};

export default Body;
