import React, { Component } from "react";
import "./App.css";

import Info from "./Components/Info";
import Setting from "./Components/Setting";
import Body from "./Components/Body";
import Footer from "./Components/Footer";

class App extends Component {
  constructor() {
    super();
    this.state = {
      IconPlay: "fas fa-pause",
      status: "paused...",
      Name: "Wegz - Asyad El Soot _ ويجز - اسياد الصوت",
      audioSrc: "audio/wegz/Wegz  - Khod W Hat  _ ويجز - خد وهات.mp3",
      statusSound: "fas fa-volume-up",
      maxV: "",
      Duration: "🎶 02 : 40 🎶",
      timeLeft: "0",
      TimeLeft: "",
      sound: 0.8,
    };
  }
  componentDidMount() {
    document.querySelector(
      ".audio"
    ).style.backgroundImage = `url(image/wegz/1.jpg)`;
    let fun_ction = function () {
      const elee = document.querySelectorAll("ul li");
      for (let i = 0; i < elee.length; i++) {
        let duration = elee[i].firstElementChild.duration;
        elee[i].lastElementChild.lastElementChild.innerText =
          duration / 60 > 9
            ? parseInt(duration / 60)
            : `🎶 0${parseInt(duration / 60)} : ` +
              (duration % 60 > 9
                ? parseInt(duration % 60) + " 🎶"
                : `0${parseInt(duration % 60)} 🎶`);
        if (
          elee[i].lastElementChild.lastElementChild.innerText.includes("NaN")
        ) {
          elee[i].lastElementChild.lastElementChild.innerText =
            "🌚loading...🌚";
        }
      }
    };
    setTimeout(() => {
      document.querySelector(".overlayTest").remove();
      if (this.state.TimeLeft !== " ") {
        // setInterval(() => {
        //   fun_ction();
        //   let ele = document.querySelector("ul li.active");
        //   let TimeLeft = ele.firstElementChild.currentTime;
        //   ele.firstElementChild.onended = () => {
        //     document.querySelector(".fa-long-arrow-alt-right").click();
        //   };
        //   this.setState({
        //     maxV: parseInt(ele.firstElementChild.duration),
        //     timeLeft: parseInt(TimeLeft),
        //     TimeLeft: `🎶 ${
        //       (TimeLeft / 60 > 9
        //         ? parseInt(TimeLeft / 60)
        //         : "0" + parseInt(TimeLeft / 60)) +
        //       ":" +
        //       (TimeLeft % 60 > 9
        //         ? parseInt(TimeLeft % 60)
        //         : "0" + parseInt(TimeLeft % 60))
        //     } `,
        //   });
        // }, 1000);
      } else {
        console.error("errr");
      }
    }, 1500);
  }
  runTrack = (e) => {
    const el = e.target;
    if (el.lastElementChild.innerText.includes("loading")) {
    } else {
      el.parentElement.querySelectorAll("li").forEach((ele) => {
        ele.classList.remove("active");
        ele.firstElementChild.pause();
      });
      el.classList.add("active");
      el.firstElementChild.load();
      el.firstElementChild.play();
      el.firstElementChild.volume = this.state.sound;
      el.parentElement.parentElement.style.backgroundImage = `url(${el.getAttribute(
        "data-src"
      )})`;
      this.setState({
        Duration: el.lastElementChild.innerText,
        IconPlay: "fas fa-play",
        Name: el.querySelector("ul li .audioName").innerText,
        status: "played...",
        audioSrc: el.firstElementChild.src,
      });
    }
  };
  play = () => {
    this.setState({ IconPlay: "fas fa-play", status: "played..." });
    document.querySelector(".audio ul li.active").firstElementChild.play();
  };
  pause = () => {
    this.setState({ IconPlay: "fas fa-pause", status: "paused..." });
    document.querySelector(".audio ul li.active").firstElementChild.pause();
  };
  off_on = () => {
    if (this.state.IconPlay.includes("fa-play")) {
      this.pause();
    } else {
      this.play();
    }
  };
  Change = (e) => {
    let traks = document.querySelectorAll(".audio ul li");
    for (let i = 0; i <= traks.length; i++) {
      if (traks[i].classList.contains("active")) {
        if (e.target.classList.contains("fa-long-arrow-alt-right")) {
          i =
            i === traks.length - 1
              ? traks[0].click()
              : traks[i].nextElementSibling.click();
          break;
        } else {
          i =
            i === 0
              ? traks[traks.length - 1].click()
              : traks[i].previousElementSibling.click();
          break;
        }
      }
    }
  };
  Speed = (e) => {
    let traks = document.querySelector(".audio ul li.active");
    if (e.target.classList.contains("fa-angle-double-right")) {
      if (traks.firstElementChild.playbackRate < 2) {
        traks.firstElementChild.playbackRate += 0.25;
        alert("The speed is " + traks.firstElementChild.playbackRate);
      }
    } else {
      if (traks.firstElementChild.playbackRate > 0.25) {
        traks.firstElementChild.playbackRate -= 0.25;
        alert("The speed is " + traks.firstElementChild.playbackRate);
      }
    }
  };
  reload = () => {
    document.querySelector(".audio ul li.active").firstElementChild.load();
    this.play();
  };
  handleChangeCurent = (e) => {
    document.querySelector(
      ".audio ul li.active"
    ).firstElementChild.currentTime = e.target.value;
    this.setState({ value: e.target.value });
  };
  handleChangeV = (e) => {
    document.querySelector(".audio ul li.active").firstElementChild.volume =
      e.target.value;
    this.setState({ sound: e.target.value });
    if (e.target.value >= 0.5) {
      this.setState({ statusSound: "fas fa-volume-up" });
    } else if (e.target.value < 0.5 && e.target.value >= 0.1) {
      this.setState({ statusSound: "fas fa-volume-down" });
    } else {
      this.setState({ statusSound: "fas fa-volume-mute" });
    }
  };
  handleMuted = (e) => {
    if (!e.target.classList.contains("fa-volume-mute")) {
      this.setState({
        statusSound: "fas fa-volume-mute",
      });
      document.querySelector("ul li.active").firstElementChild.volume = 0;
    } else {
      if (e.target.nextElementSibling.value >= 0.1) {
        document.querySelector("ul li.active").firstElementChild.volume =
          this.state.sound;

        this.setState({
          statusSound:
            e.target.nextElementSibling.value < 0.5
              ? "fas fa-volume-down"
              : "fas fa-volume-up",
        });
      }
    }
  };

  render() {
    return (
      <React.Fragment>
        <div className="overlayTest">
          <i className="fas fa-spinner fa-pulse fa-2x"></i>
        </div>
        <div className="overlay"></div>
        <div className="container">
          <Info status={this.state.status} Name={this.state.Name} />
          <Setting
            sound={this.state.sound}
            value={this.state.value}
            reload={this.reload}
            handleChangeCurent={this.handleChangeCurent}
            handleChangeV={this.handleChangeV}
            handleMuted={this.handleMuted}
            Duration={this.state.Duration}
            timeLeft={this.state.timeLeft}
            TimeLeft={this.state.TimeLeft}
            maxV={this.state.maxV}
            statusSound={this.state.statusSound}
            off_on={this.off_on}
            Change={this.Change}
            Speed={this.Speed}
            IconPlay={this.state.IconPlay}
            audioSrc={this.state.audioSrc}
          />
          <Body runTrack={this.runTrack} />
        </div>
        <Footer />
      </React.Fragment>
    );
  }
}

export default App;
