import React from "react";
import "./style.css";
const Header = (props) => {
  return (
    <div className="header">
      <section className="setting">
        <i className="fas fa-redo-alt reload" onClick={props.reload}></i>
        <input
          type="range"
          min="0"
          step="2"
          max={props.maxV}
          value={props.timeLeft}
          onChange={props.handleChangeCurent}
        />
        <div className="spanData">
          <span className="spanTimeLeft">{props.TimeLeft}</span>
          <span className="spanTime">{props.Duration}</span>
        </div>
        <i className={props.statusSound} onClick={props.handleMuted}></i>
        <input
          type="range"
          min="0"
          max="1"
          step="0.1"
          value={props.sound}
          onChange={props.handleChangeV}
        />
        <a href={props.audioSrc} download className="download">
          <i className="fas fa-arrow-down download"></i>
        </a>
      </section>

      <section className="settingTow">
        <i className="fas fa-long-arrow-alt-left" onClick={props.Change}></i>
        <i className="fas fa-angle-double-left" onClick={props.Speed}></i>
        <i className={props.IconPlay} onClick={props.off_on}></i>
        <i className="fas fa-angle-double-right" onClick={props.Speed}></i>
        <i className="fas fa-long-arrow-alt-right" onClick={props.Change}></i>
      </section>
    </div>
  );
};

export default Header;
